import {
	Grid,
	// Container,
	// Typography,
} from "@material-ui/core";
import MailIcon from '@material-ui/icons/Mail';
import TwitterIcon from '@material-ui/icons/Twitter';

import * as React from "react";

export default class TopGenres extends React.Component<{}, {}> {

	render() {
		return (
			<div className="footer">
				<Grid container
					direction="row"
					justify="center"
					className="genres-heading">
					<Grid item xs={4}>
						<div className="footer-actions">
							<a target="_blank" href="https://forms.gle/VYwx5bT7K9qJ6GHC9" >
								Add More Bands
							</a>
						</div>
						<div className="footer-actions">
							FAQ
						</div>
					</Grid>
					<Grid item  xs={4} style={{textAlign: "right"}}>
						<div className="footer-actions" style={{"marginBottom":"8px"}}>
							<a href="mailto:hardikjuneja.hj@gmail.com">
							    <MailIcon style={{color: 'white'}}/>
							</a>
						</div>
						<div className="footer-actions" style={{"marginTop":"8px"}} >
							<a target="_blank" href="https://twitter.com/_imdass">
							    <TwitterIcon style={{color: 'white'}}/>
							</a>
						</div>
					</Grid>
				</Grid>
			</div>
		)
	}
}



