import {
  Box,
  Typography,
  Grid,
  Container,
  Tooltip,
} from "@material-ui/core";

import * as React from "react";
import ArtistsCard from './ArtistsCard';
import { BuyCoffeeCard } from './MyCard';
import ReactRotatingText from 'react-rotating-text';
import HelpOutlineIcon from '@material-ui/icons/HelpOutline';

import './Artists.css';

type props = {
  coreArtists: any[];
  otherArtists: any[];
  cwArtists: {};
}

const initialState = {
  finalCoreArtists: [],
  finalOtherArists: [],
}

export default class Artists extends React.Component<props, typeof initialState> {
  state = { ...initialState };

  componentDidMount () {
    this.finaliseArtists();
  }

  componentWillUnmount () {
  	const bodyElt = document.querySelector("body");
  	if(bodyElt != null) {    
  		bodyElt.style.backgroundColor = "white";
  	}
  }


  finaliseArtists = () => {
    const { coreArtists, otherArtists } = this.props;

    // metal listeners
    // if more than 30 only give 20
    const newCoreArtists = coreArtists.slice(0,20);

    // if newCoreArtists is less than 20, then get remaining otherArtists
    let newOtherArtists = [];
    if (newCoreArtists.length < 20) { 
      const length = 20 - newCoreArtists.length
      newOtherArtists = otherArtists.slice(0, length) 
    }
    this.setState({finalCoreArtists: newCoreArtists, finalOtherArists: newOtherArtists})
  }

  searchMerch = (query: string, type:string) => {

  	const { cwArtists } = this.props;

    // search in the csdata if there's a url for this artist
    query = String(query).replace(/['"]+/g, '');
    if (cwArtists[query]) {
      const  merch = cwArtists[query].merch;
      if (merch === ".") {
        window.open("https://www.google.com/search?q="+encodeURIComponent(query)+"+band+merch")
      } else {
        window.open(cwArtists[query].merch);
      }
    } else if (type === "not-metal") {
      window.open("https://www.google.com/search?q="+encodeURIComponent(query)+"+band+merch")
    } else {
      window.open("https://www.impericon.com/en/catalogsearch/result/index/?cat=4235&q=" + encodeURIComponent(query));
    }
  };

  searchBandcamp = (query: string) => {

  	const { cwArtists } = this.props;

    // search in the csdata if there's a url with .bandcamp.com for this artist
    query = String(query).replace(/['"]+/g, '');
    if (cwArtists[query]) {
      const  music = cwArtists[query].bandcamp;
      if (music === ".") {
        window.open("https://www.google.com/search?q="+encodeURIComponent(query)+"+Music")
      } else {
        window.open(music);
      }
    } else {
      window.open("https://bandcamp.com/search?q=" + encodeURIComponent(query));
    }
  };

  render() {
  	const { finalCoreArtists, finalOtherArists } = this.state;

    const bodyElt = document.querySelector("body");
  	if(bodyElt != null) {    
  		bodyElt.style.backgroundColor = "rgba(255, 91, 91, 0.92)";
  	}

    const question = (
      <Tooltip title="Only metalcore, hardcore, djent, punk artists have direct links" enterTouchDelay={30} enterDelay={500} leaveDelay={200}> 
        <span className="question-mark"> <HelpOutlineIcon /> </span>
      </Tooltip>
    )

    return (
      <Container maxWidth="lg" className="artist-container">
        <Grid container spacing={8} >
          <Grid item xs={12}>
            <Box alignItems="center" justifyContent="center" display="flex" className="heading-artists" >
              <Typography variant="h2">
                Support Your Favorite <ReactRotatingText items={['Djent', 'Metalcore', 'Hardcore', 'Metal']} className="artist-metal-text" /> Artists
              </Typography>
            </Box>
          </Grid>
          {(finalCoreArtists.length < 20 && finalCoreArtists.length > 0) ?
            <Grid item xs={12} alignItems="center">
              <Box alignItems="center" justifyContent="center" display="flex" className="subheading-artists" >
                <Typography variant="h5">
                  <b>Favorite Metal Artists</b> {question}
                </Typography>
              </Box>
            </Grid> : ""
          }
          {finalCoreArtists.map( (artist, index ) => (
            <Grid item xs>
               { (Object.keys(artist).length === 0 && artist.constructor === Object) ?  
                    <BuyCoffeeCard /> :
                    <ArtistsCard artistData={artist} searchMerch={this.searchMerch} searchBandcamp={this.searchBandcamp} type="metal"/>
                  }
            </Grid>
          ))}

          {
            (finalCoreArtists.length < 20) ? 
              (<Grid item xs={12} alignItems="center">
                <Box alignItems="center" justifyContent="center" display="flex" className="subheading-artists" >
                  <Typography variant="h5">
                    <b>Favorite Non Metal Artists</b> {question}
                  </Typography>
                </Box>
              </Grid>
            ) : ""
          }

          {
            (finalCoreArtists.length < 20) ? 
              finalOtherArists.map( (artist, index ) => (
                <Grid item xs>
                  <ArtistsCard artistData={artist} searchMerch={this.searchMerch} searchBandcamp={this.searchBandcamp} type="not-metal"/>
                </Grid>
              )
            ) : ''
          }
        </Grid>
      </Container>
    )
  }
}


