import {
  Grid,
  Select,
  FormControl,
  InputLabel,
  MenuItem
} from "@material-ui/core";

import axios from 'axios';
import * as React from "react";
import SpotifyWebApi from "spotify-web-api-js";

import Login from './Components/Login';
import Artists from './Components/Artists';
import TopGenres from './Components/TopGenres';
import Footer from './Components/Footer';


const initialState = {
  authenticated: false,
  coreArtists: undefined as any[] | undefined,
  cwArtists: {},
  limit: 50,
  otherArtists: undefined as any[] | undefined,
  timeRange: "medium_term",
  topGenres: undefined as any[] | undefined,
};

//  topTracks: undefined as SpotifyApi.UsersTopTracksResponse | undefined,

const coreGenres = [/.*(hardcore)/, /.*(metalcore)/, /.*(djent)/, /.*(metal)/, /.*(punk)/]

class App extends React.Component<{}, typeof initialState> {
  state = { ...initialState };
  spotifyApi: SpotifyWebApi.SpotifyWebApiJs;

  // componentDidMount
  componentDidMount() {
    if (window.location.hash) {
      window.location.hash
        .slice(1)
        .split("&")
        .forEach(kv => {
          const [key, value] = kv.split("=");
          if (key === "access_token") {
            this.setupSpotifyClient(value);
            this.getData();
            this.getGenreData();

            // Construct a list of artists from the sheets
            axios.get('https://spreadsheets.google.com/feeds/cells/1EWfXvFqZG3xw3fS5M3cxkn9W-vSK6f5FVfDaquZ_1mI/1/public/values?alt=json')
            .then((response) => {
              const cwArtists = {}
              response.data.feed.entry.forEach( (entry: any, count: number) => {
                if (count % 3 === 0) {
                  if (entry.content.$t && response.data.feed.entry[count+1] && response.data.feed.entry[count+2]) {
                    const rkey = entry.content.$t.trim().replace(/['"]+/g, '');
                    const bandcamp = response.data.feed.entry[count+1].content.$t.trim().replace(/['"]+/g, '')
                    const merch = response.data.feed.entry[count+2].content.$t.trim().replace(/['"]+/g, '')
                    cwArtists[rkey] = { bandcamp , merch };
                  }
                }
              })
              this.setState({cwArtists})
            });
          }
        });
    }
  }

  setupSpotifyClient = (accessToken: string) => {
    this.spotifyApi = new SpotifyWebApi();
    this.spotifyApi.setAccessToken(accessToken);

    this.setState({ authenticated: true });
  };

  /*
    Get user data
  */

  /*
    - create an array of artists, 
  */

  getData = async () => {
    try {

      // get top artists 
      const [coreArtists, otherArtists] = await Promise.all([
        this.spotifyApi.getMyTopArtists({
          limit: this.state.limit,
          time_range: this.state.timeRange
        }),
      ])
      .then(async ([topArtists]) => {

        const internalcoreArtists: any[] = [];
        const internalotherArtists: any[] = [];
        // get artist genres and match
        topArtists.items.map((artist) => {
          const isCoreArtists = coreGenres.some( (genre) => {
            return artist.genres.some(e => genre.test(e));
          })
          isCoreArtists ? internalcoreArtists.push(artist) : internalotherArtists.push(artist)
        })

        return [internalcoreArtists, internalotherArtists];
      })

      // do this for buyMeACoffee
      if (coreArtists.length )
        coreArtists.splice(19, 0, {});

      this.setState({ coreArtists, otherArtists });
    } catch (e) {
      console.error(e); //tslint:disable-line
      this.setState({ authenticated: false });
    }
  };

  getUserData = () => {
    try {
      // get top artists 
      this.spotifyApi.getMyTopArtists({
        limit: 40,
        time_range: "long_term"
      })
    } catch (e) {
      console.error(e); //tslint:disable-line
      // this.setState({ authenticated: false });
    }
  }

  getGenreData = async () => {
    try {
      // get top artists 
      const topGenres = await Promise.all([
        this.spotifyApi.getMyTopArtists({
          limit: 50,
          time_range: "long_term"
        }),
      ])
      .then(async ([topArtists]) => {
        let internalTopGenres: any[] = [];

        // get artist genres and match
        topArtists.items.map((artist) => {
          internalTopGenres = internalTopGenres.concat(artist.genres)
        })
        return this.sortByFrequency(internalTopGenres);
      })

      this.setState({ topGenres });
    } catch (e) {
      console.error(e); //tslint:disable-line
      this.setState({ authenticated: false });
    }
  };

  sortByFrequency = (array: any[]) => {
      const frequency = {};

      array.forEach((value) => { frequency[value] = 0; });

      const uniques = array.filter((value) => {
          return ++frequency[value] === 1;
      });

      return uniques.sort((a, b) => {
          return frequency[b] - frequency[a];
      });
  }

  renderHeader = () => (
    <Grid item xs={12}>
      <FormControl>
        <InputLabel htmlFor="time-range">Time range</InputLabel>
        <Select
          value={this.state.timeRange}
          onChange={this.handleTimeRangeChange}
          inputProps={{
            name: "time-range"
          }}
          autoWidth
        >
          <MenuItem value="short_term">Short term</MenuItem>
          <MenuItem value="medium_term">Medium term</MenuItem>
          <MenuItem value="long_term">Long term</MenuItem>
        </Select>
      </FormControl>

      <FormControl>
        <InputLabel htmlFor="limit">Items</InputLabel>
        <Select
          value={this.state.limit}
          onChange={this.handleLimitChange}
          inputProps={{
            name: "limit"
          }}
          autoWidth
        >
          <MenuItem value="10">10</MenuItem>
          <MenuItem value="20">20</MenuItem>
          <MenuItem value="35">35</MenuItem>
          <MenuItem value="50">50</MenuItem>
        </Select>
      </FormControl>
    </Grid>
  );

  handleTimeRangeChange = (timeRange: React.ChangeEvent<HTMLSelectElement>) => {
    this.setState({ timeRange: timeRange.target.value }, () => this.getData());
  };

  handleLimitChange = (limit: React.ChangeEvent<HTMLSelectElement>) => {
    this.setState({ limit: parseInt(limit.target.value, 10) }, () =>
      this.getData()
    );
  };

  renderTopArtists = () => {
    const { coreArtists, otherArtists, cwArtists } = this.state;
    if (!coreArtists) return null;
    if (!otherArtists) return null;

    return <Artists coreArtists={coreArtists} otherArtists={otherArtists} cwArtists={cwArtists}/>
  };

  renderTopGenres = () => {
    const { topGenres } = this.state;
    return <TopGenres topGenres={topGenres} />
  }

  render() {
    const { authenticated } = this.state;

    // {this.renderHeader()}

    // Component Login if not authenticated else Show main page
    return (
      <React.Fragment>
        {authenticated ? (
          <React.Fragment>
            <Grid container spacing={2}>
              {this.renderTopGenres()}
            </Grid>         
            <Grid container spacing={2}>
              {this.renderTopArtists()}
            </Grid>
            <Footer />
          </React.Fragment>
        ) : (
          <Login />
        )}
      </React.Fragment>
    );
  }
}

export default App;



