import {
  Card,
  Button,
  CardContent,
  Typography,
  Grid,
  Tooltip,
} from "@material-ui/core";

import * as React from "react";
import './Artists.css';

type props = {
	artistData: {
		name: string,
		images: any[],
		genres: any[],
		popularity: any,
	};
	searchMerch: (query: string, type: string) => void;
	searchBandcamp: (query: string) => void;
	type: string;
}

export default class ArtistsCard extends React.Component< props, {}> {

	definePop = (popularity: any) => {

		if (popularity === undefined) {
			return 0;
		}

		if ( popularity >= 0  && popularity < 30 ) {
			return 1;
		} else if (  popularity > 29  && popularity < 55 ) {
			return 2;
		} else if ( popularity > 54 && popularity < 75 ) {
			return 3;
		} else if ( popularity > 74  && popularity < 90 ) {
			return 4;
		} else {
			return 5;
		};
	};

	render() {
		const { artistData, searchMerch, searchBandcamp, type} = this.props;

		return (
			<div className="card-container">
				<Card className="artist-card">
					<div>
						<img
							style={{height: '160px', width: '160px'}}
							src={artistData.images[2].url}
							className="artist-card-image"
							title="artist image"
						/>
						<CardContent className="artist-card-image artist-card-name">
							<Typography component="h6" variant="h6">
								<div className="name-card">{artistData.name}</div>
							</Typography>
							<Typography variant="body2" className="artist-card-genre">
								<Tooltip title={artistData.genres.join(' / ')} enterTouchDelay={30}>
									<div className="name-card subname-card"> {artistData.genres.join(' / ')} </div>
								</Tooltip>
							</Typography>
						</CardContent>
					</div>
					<Grid container 
						direction="row"
						justify="flex-end"
						alignItems="center"
						className="card-actions">
						<Grid item className="artist-card-genre-grid">
							<CardContent>
								<Typography variant="body2" className="artist-card-genre">
									<b>Popularity</b> : {"★".repeat(this.definePop(artistData.popularity))}
								</Typography>
							</CardContent>
						</Grid>
						<Grid item>
							<CardContent>
								<Button
									variant="outlined"
									color="primary"
									className="artist-card-button"
									onClick={() => searchMerch(`"${artistData.name}"`, type)} >
									Buy Merch
								</Button>
							</CardContent>
						</Grid>
						<Grid item>
							<CardContent>
								<Button
									variant="outlined"
									color="primary"
									className="artist-card-button"
									onClick={() => searchBandcamp(artistData.name)} >
									Buy Music
								</Button>
							</CardContent>
						</Grid>
					</Grid>
				</Card>
			</div>
		)
	}
};

