import {
  Grid,
  Typography,
  Box,
} from "@material-ui/core";
import { createMuiTheme, responsiveFontSizes, ThemeProvider } from '@material-ui/core/styles';

import * as React from "react";
import './login.css';
import logo from '../images/supportify.png'

let theme = createMuiTheme();
theme = responsiveFontSizes(theme);

export default class Login extends React.Component<{}, {}> {
  // hjuneja
  authenticate = () => {
    const clientId = 'ea3a08af2873400ca4085d3bbecdefda';
    let redirectUri = ''
      

    if (window.location.host === 'localhost:3000') {
    	redirectUri = 'http://localhost:3000/';
    } else {
    	//if (window.location.host === 'https://supportify.club/') {
    	redirectUri = 'https://supportify.club/';
    }
    // } else {
    // 	redirectUri = 'https://hardikj.github.io/supportify-deploy/';
    // }

    window.location.href =
      `https://accounts.spotify.com/authorize?client_id=${clientId}&redirect_uri=${encodeURIComponent(redirectUri)}&scope=user-top-read&response_type=token`;
  };

  componentWillUnmount () {
  	const bodyElt = document.querySelector("body");
	if(bodyElt != null) {    
		bodyElt.style.backgroundColor = "white";
	}
  }

  render() {
	const bodyElt = document.querySelector("body");
	if(bodyElt != null) {    
		bodyElt.style.backgroundColor = "black";
	}
    return (
		<div className="login-container">
			<Grid container
				direction="column"
				alignItems="center">
				<h1 className="heading" > <img src={logo}/></h1>
			</Grid>
			<Grid
				container
				spacing={5}
				direction="column"
				alignItems="center"
				justify="center" >
				<Grid item>
					<Box border={5} color={"white"} className="login-box">
						<ThemeProvider theme={theme}>
							<Typography variant="h4">
								A T-SHIRT IS THE EQUIVALENT TO 5000 STREAMS ON SPOTIFY.
								76% OF ALL MUSIC IN 2019 IS STREAMED AND NOT BOUGHT.
								BAND MERCHANDISE IS THE MOST DIRECT WAY OF SUPPORTING AN ARTIST
							</Typography>
						</ThemeProvider>
					</Box>
				</Grid>
				<Grid item>
					<button
						className="login-button"
						onClick={this.authenticate}>
						Login With Spotify
					</button>
				</Grid>
			</Grid>
		</div>
	)
  }
}



