import {
	Grid,
	Box,
	Typography,
	TableContainer,
	Table,
	TableBody,
	TableRow,
	TableCell,
} from "@material-ui/core";
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import * as React from "react";

import './Artists.css';
// import logo from '../images/supportify.png';

// <Grid container item xs={3}>
// 	<Box className="genres-image">
// 		<Avatar alt="Remy Sharp" src={logo} />
// 	</Box>
// </Grid>

type props = {
	topGenres: any[] | undefined;
}

export default class TopGenres extends React.Component<props, {}> {



	render() {
	
		const {topGenres} = this.props;
		let genres1: any[] = [];
		let genres2: any[] = [];
		if (topGenres) {
			genres1 = topGenres.slice(0, 5);
			genres2 = topGenres.slice(5, 10);
		}
		return (
			<div className="genres-main">
				<Grid container
					direction="row"
					justify="center"
					alignItems="center"
					className="genres-heading">
					<Grid container item xs={8} sm={8}>
						<Box textAlign="center" color={"white"} className="genres-heading">
								<Typography variant="h3">
									Welcome, Let's have a look at your favorite genres 
								</Typography>
						</Box>
					</Grid>
				</Grid>
				<Grid container
					spacing={3}
					direction="row"
					justify="center"
					alignItems="center"
					className="genres-table">

					<Grid container item xs={5}>
						<TableContainer>
							<Table aria-label="simple table">
								<TableBody>
									{genres1.map((row, i) => (
										<TableRow key={row}>
											<TableCell align="left" className='genres-color fontsize-genres'><b className='fontsize-genres'>{i+1 + ".  " +row.toUpperCase()}</b></TableCell>
										</TableRow>
									))}
								</TableBody>
							</Table>
						</TableContainer>
					</Grid>
					<Grid container item xs={5} className="second-table">
						<TableContainer>
							<Table aria-label="simple table">
								<TableBody>
									{genres2.map((row, i) => (
										<TableRow key={row}>
											<TableCell align="left" className='genres-color fontsize-genres'><b className='fontsize-genres'>{i+6+".  "+row.toUpperCase()}</b></TableCell>
										</TableRow>
									))}
								</TableBody>
							</Table>
						</TableContainer>
					</Grid>
				</Grid>
				<Grid container
					justify="center"
					alignItems="center">

					<Box textAlign="center" color={"white"}  style={{marginTop: "7vw"}}>
						<div> SCROLL DOWN </div>
						<ExpandMoreIcon color={"inherit"} fontSize="large" />
					</Box>
				</Grid>
			</div>
		)
	}
}


