import * as React from "react";

class BuyCoffeeCard extends React.Component< {}, {}> {

	render() {
		return (
			<div className="card-container">
				<Card className="artist-card" style={{padding:"81px"}}>
					<a className="bmc-button" target="_blank" href="https://www.buymeacoffee.com/dass" >
						<img src="https://cdn.buymeacoffee.com/buttons/bmc-new-btn-logo.svg" alt="Buy me a tea" />
						<span style={{"marginLeft":"5px", fontSize:"19px !important"}}>Buy me a coffee</span>
					</a>
				</Card>
			</div>
		)
	}
};

